<template>
  <div>
    <section class="category">
      <h1>{{ cat.name }}</h1>
      <picture>
        <img v-bind:src="`/images/${cat.frontthumb}`" v-bind:alt="cat.name">
      </picture>
      <p>{{ cat.description }}</p>
    </section>
    
    <section class="subcategory">
      <h3>My experiences for {{ cat.name }}</h3>
      <div class="cards">
        <router-link
          v-for="subc in cat.subcategory"
          v-bind:key="subc.slug"
          v-bind:to="{ name: 'subc.show', params: { subcategorySlug: subc.slug } }"
        >
          <SubcategoryCard 
            
            v-bind:subc="subc"
          />
        </router-link>
      </div>
    </section>
  </div>
</template>

<script>
import sourceData from '@/data.json'
import SubcategoryCard from '@/components/SubcategoryCard.vue'
//console.log(sourceData) // Object { category: (4) […] }
//console.log(SubcategoryCard) // props: Object{ subcategory: Object { type: Object(), required: true } }

export default {
  
  components: {
    SubcategoryCard
  },
  props: {
    id: {
      type: Number,
      required: true
    }
  },
  computed: {
    // categoryId() {
    //   return parseInt(this.$route.params.id);
    // },
    cat() {
      return sourceData.category.find( 
        cat => cat.id === this.id
      )
    }
  }

}
</script>

