<template>
  
  <section class="subcategory-view">
    <h1>{{ subc.name }}</h1>
    <GoBack />
    <picture>
      <img v-bind:src="`/images/${subc.thumb}`" v-bind:alt="subc.name">
    </picture>
    <p>{{ subc.desc }}</p>
    <div class="subcategory-gallery">
      <!-- <button>Gallery</button> -->
      <picture v-for="(g, idx) in subc.gallery" v-bind:key="idx">
        <img v-bind:src="`/images/${g}`" v-bind:alt="subc.name+idx" @click="openModal">
      </picture>
      <Modal 
        v-if="isOpened"
        @close="closeModal"
      >
        <template v-slot:image>
          <img src="" alt="">
        </template>
      </Modal>
    </div>
  </section>

</template>

<script>
import sourceData from '@/data.json'
import GoBack from '@/components/GoBack.vue'
//import GalleryCard from '@/components/GalleryCard.vue'
import Modal from '@/components/Modal.vue'

export default {
  data() {
    return {
      isOpened: false
    }
  },
  methods: {
    openModal() {
      this.isOpened = true
    },
    closeModal() {
      this.isOpened = false
    }
  },
  components: {
    GoBack,
    //GalleryCard
    Modal
  },
  props:{
    id: {type: Number, required: true},
    subcategorySlug: {type: String, required: true}
  },
  computed:{
    cat() {
      return sourceData.category.find( 
        cat => cat.id === this.id
      )
    },
    subc() {
      return this.cat.subcategory.find(
        subc=> subc.slug === this.subcategorySlug
      )
    }
  }
}
</script>

<style lang="scss" scoped>

.subcategory-view {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1rem;
  h1,
  .subcategory-gallery {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
    img {
      cursor: pointer;
    }
    @media screen and (min-width: 40rem) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media screen and (min-width: 60rem) {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  picture {
    img {
      aspect-ratio: 4 / 3;
      object-fit: cover;
    }
  }
  @media screen and (min-width: 30rem) {
    grid-template-columns: repeat(2, 1fr);
  }
}

</style>
