<template>
  
  <div id="nav">
    <div class="toggler" @click="openMenu">
      <img src="bar.svg" alt="toggle menu">
    </div>
    <transition name="slide">
      <nav class="navigation" v-if="isOpen">
        <router-link to="/">Home</router-link>
        <router-link
          v-for="c in category"
          v-bind:key="c.id"
          v-bind:to="{name: 'category.show', params: {id: c.id, slug: c.slug}}"
        >
          {{ c.name }}
        </router-link>
      </nav>
    </transition>
    
    <nav class="navigation-desktop" v-if="!isMobile">
      <router-link to="/">Home</router-link>
      <router-link
        v-for="c in category"
        v-bind:key="c.id"
        v-bind:to="{name: 'category.show', params: {id: c.id, slug: c.slug}}"
      >
        {{ c.name }}
      </router-link>
    </nav>
    <div class="user">
      <router-link v-bind:to="{name: 'protected'}">
        <img src="user.svg" alt="login">
      </router-link>
    </div>
  </div>

</template>

<script>
import sourceData from '@/data.json'

export default {

  data() {
    return {
      category: sourceData.category,
      isMobile: false,
      isOpen: false
    }
  },
  // watch: {
  //   $route() {
  //     this.isOpen = false
  //   }
  // },
  watch:{
    $route (to, from){
      this.isOpen = false;
    }
  },
  created() {
    window.addEventListener( 'resize', this.checkScreen )
    this.checkScreen()
  },
  methods: {
    openMenu() {
      this.isOpen = !this.isOpen
    },
    checkScreen() {
      this.windowWidth = window.innerWidth
      if ( this.windowWidth <= 640 ) {
        this.isMobile = true
        return
      }
      this.isMobile = false
      this.isOpen = false
      return
    }
  }
  
}
</script>


