<template>
  
  <div>
    <h1>Protected area</h1>
    <hr>
    <h3>Hello, {{ username }}</h3>
    <button @click="logout">Logout</button>
  </div>


</template>

<script>

export default {
  
  data() {
    return {
      username: window.user
    }
  },
  methods: {
    logout() {
      window.user = null
      this.$router.push({ name: 'Home' })
    }
  }

}
</script>
