import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import sourceData from '@/data.json'
import Protected from '@/views/Protected.vue'
import Login from '@/views/Login.vue'
// import Me from '@/views/Me.vue'
// import Cosplay from '@/views/Cosplay.vue'
// import Beautifull from '@/views/Beautifull.vue'
// import Adult from '@/views/Adult.vue'
import CategoryAll from '@/views/CategoryAll.vue'
import SubcategoryView from '@/views/SubcategoryView.vue'
import NotFound from '@/views/NotFound.vue'

const routes = [
  { path: '/', name: 'Home', component: Home },
  // { path: '/about', name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: function () {
  //     return import(/* webpackChunkName: "about" */ '../views/About.vue')
  //   }
  // },
  // { path: '/me', name: 'Me', component: Me },
  // { path: '/cosplay', name: 'Cosplay', component: Cosplay },
  // { path: '/beautifull', name: 'Beautifull', component: Beautifull },
  // { path: '/adult', name: 'Adult', component: Adult },
  {
    path: '/protected',
    name: 'protected',
    component: Protected,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  { path: 
    '/category/:id/:slug', 
    name: 'category.show', 
    component: CategoryAll,
    props: route => ({ ...route.params, id: parseInt(route.params.id) }),
    beforeEnter(to, from) {
      const exist = sourceData.category.find(
        cat => cat.id === parseInt(to.params.id)
      )
      if(!exist) return { 
        name: 'NotFound',
        // allows keeping the URL while rendering a different page
        params: { pathMatch: to.path.split('/').slice(1) },
        query: to.query,
        hash: to.hash,
      }
    }
  },
  { path: 
    '/category/:id/:slug/:subcategorySlug', 
    name: 'subc.show', 
    component: SubcategoryView,
    props: route => ({ ...route.params, id: parseInt(route.params.id) })
  },
  { 
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: NotFound
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  linkActiveClass: 'active',
  scrollBehavior (to, from, savedPosition) {
    return savedPosition || new Promise((resolve)=>{
      setTimeout(()=> resolve({ top:0 }), 300)
    })
  }
})

router.beforeEach((to, from) => {
  if ( to.meta.requiresAuth && !window.user ) {
    return {name: 'login'}
  }
})

export default router
